        </el-menu-item-group>
        <el-submenu index="3-4">
          <template slot="title">选项4</template>
          <el-menu-item index="3-4-1">选项4-1</el-menu-item>
        </el-submenu>
      </el-submenu>
    </el-menu> -->
    <el-col :span="24">
    <h5>自定义颜色</h5>
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
      style="height:500px">
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>导航一</span>
        </template>
        <el-menu-item-group>
          <template slot="title">分组一</template>
          <el-menu-item index="1-1" @click="manager">后台管理</el-menu-item>
          <el-menu-item index="1-2" @click="register">订单管理</el-menu-item>
        </el-menu-item-group>
        <el-menu-item-group title="分组2">
          <el-menu-item index="1-3">选项3</el-menu-item>
        </el-menu-item-group>
        <el-submenu index="1-4">
          <template slot="title">选项4</template>
          <el-menu-item index="1-4-1">选项1</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">导航二</span>
      </el-menu-item>
      <el-menu-item index="3" disabled>
        <i class="el-icon-document"></i>
        <span slot="title">导航三</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">导航四</span>
      </el-menu-item>
    </el-menu>
  </el-col>

  </el-aside>
  
  <el-container>
    <el-header style="text-align: right; font-size: 12px">
      <span>王小虎</span>
      <el-button type="text" @click="save()" style="color:yellow">添加</el-button>
    </el-header>
    <el-main>
       <el-table
    :data="tableData.filter(data => !search || data.username.toLowerCase().includes(search.toLowerCase()))"
    style="width: 100%;" max-height="300px" >
    <el-table-column label="编号" prop="id"></el-table-column>
    <el-table-column label="日期" prop="date"></el-table-column>
    <el-table-column label="姓名" prop="username"></el-table-column>
    <el-table-column label="性别" prop="sex"></el-table-column>
    <el-table-column label="省份" prop="province"></el-table-column>
    <el-table-column label="市区" prop="urban"></el-table-column>
    <el-table-column label="地址" prop="address"></el-table-column>
    <el-table-column
      align="right">
      <template slot="header" slot-scope="scope">
        <el-input
          v-model="search"
          size="mini"
          placeholder="输入关键字搜索"/>
      </template>
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">修改</el-button>
        <!-- <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button> -->
        <el-popconfirm
  confirm-button-text='确认'
  cancel-button-text='取消'
  icon="el-icon-info"
  icon-color="red"
  @confirm="handleDelete(scope.$index, scope.row)"
  title="确认要删除吗？"
>
  <el-button slot="reference" size="mini" type="danger"  >删除</el-button>
</el-popconfirm>
      </template>
  
      
    </el-table-column>
  </el-table>
  
<div class="block">
  <!-- <span class="demonstration">大于 7 页时的效果</span> -->
  <el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
   :page-sizes="sizes"
   :page-size="page_size"
   :current-page="currentPage"
    layout="total, sizes, prev, pager, next, jumper"
     background
    :total="total" style="float:right;margin-top:10px">
  </el-pagination>
</div>
  <!-- Form -->


<el-dialog title="用户信息" :visible.sync="dialogFormVisible2">
  <el-form :model="form" :rules="rules"  ref="form" label-width="100px" class="demo-ruleForm">
      <el-form-item label="日期" prop="date" style="width:300px ">
        <el-date-picker type="date" placeholder="选择日期"  value-format="yyyy-MM-dd" v-model="form.date" style="width: 100%;"></el-date-picker>
      </el-form-item>
    <el-form-item label="姓名" prop="username" style="width:300px">
      <el-input v-model="form.username" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="性别" prop="sex" style="width:300px">
    <el-radio-group v-model="form.sex">
      <el-radio label="男" ></el-radio>
      <el-radio label="女"></el-radio>
    </el-radio-group>
    </el-form-item>
    <el-form-item label="省份" prop="province" style="width:300px">
        <el-input v-model="form.province" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="市区" prop="urban" style="width:300px">
        <el-input v-model="form.urban" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="地址" prop="address" style="width:300px">
      <el-input type="textarea" v-model="form.address"></el-input>
    </el-form-item>
 </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible2 = false">取 消</el-button>
    <el-button type="primary" @click="submitForm('form')">确 定</el-button>
  </div>
</el-dialog>
  <el-dialog title="用户信息" :visible.sync="dialogFormVisible">
  <el-form :model="ruleForm"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="姓名" prop="username" style="width:300px">
      <el-input v-model="ruleForm.id" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="日期" prop="date" style="width:570px" >
    <el-col :span="11">
      <el-form-item prop="date1">
        <el-date-picker type="date" placeholder="选择日期"  value-format="yyyy-MM-dd" v-model="ruleForm.date" style="width: 100%;"></el-date-picker>
      </el-form-item>
    </el-col>
    </el-form-item>
    <el-form-item label="姓名" prop="username" style="width:300px">
      <el-input v-model="ruleForm.username" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="性别" prop="sex" style="width:300px">
    <el-radio-group v-model="ruleForm.sex">
      <el-radio label="男" ></el-radio>
      <el-radio label="女"></el-radio>
    </el-radio-group>
    </el-form-item>
    <el-form-item label="省份" prop="province" style="width:300px">
        <el-input v-model="ruleForm.province" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="市区" prop="urban" style="width:300px">
        <el-input v-model="ruleForm.urban" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="地址" prop="address" style="width:300px">
      <el-input type="textarea" v-model="ruleForm.address"></el-input>
    </el-form-item>
    <!-- <el-form-item style="width:100px">
    <el-button type="primary" @click="submitForm('ruleForm')">确认添加</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
    </el-form-item>  -->
 </el-form>
 <div slot="footer" class="dialog-footer">
    <el-button @click="dialogFormVisible = false">取 消</el-button>
    <el-button type="primary" @click="update()">确 定</el-button>
</div>
 </el-dialog>
    </el-main>
  </el-container>
</el-container>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    name:'Index',
    data(){
      
      return{
          search:'',
          tableData: [],
          dialogFormVisible:false,
          ruleForm: {
          id:'',
          date:'',
          username:'',
          sex:'男',
          province:'',
          urban:'',
          address:'',
        },
        dialogFormVisible2: false,
        form: {
          date:'',
          username:'',
          sex:'男',
          province:'',
          urban:'',
          address:'',
       
        },
        rules:{
          username: [
            { required: true, message: '请输入用户姓名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          date: [
            { required: true, message: '请输入日期', trigger: 'blur' },
          ],
          province:[{required:true,message:'请输入省份',trigger:'blur'},],
          urban:[{required:true,message:'请输入市区',trigger:'blur'},],
          address:[{required:true,message:'请输入详细地址',trigger:'blur'},],
          },
      currentPage:1,
      page_size:4,
      sizes:[2,4,6,8],
      total:20,
      }
    },
    methods:{
      manager(){
        this.$router.push('/index')
      },
      register(){
        this.$router.push("/")
      },
      gettotal(){
          axios({
            method:'get',
            url:'http://localhost:9090/findbypage?pagenow='+this.currentPage+'&pagesize='+this.page_size,
            

          }).then(res=>{
            this.total=res.data.totals
          })
      },
      handleSizeChange(val) {
          this.page_size=val
      },
      handleCurrentChange(val) {
        this.currentPage=val
        console.log(`当前页: ${val}`);
        console.log(this.currentPage,this.page_size)
        axios({
          method:'get',
          url:'http://localhost:9090/findbypage?pagenow='+this.currentPage+'&pagesize='+this.page_size,
          
        }).then(res=>{
          console.log(res.data)
          this.tableData=res.data.users
          this.total=res.data.totals
          
        })
        
      },
      update(){
        console.log(this.ruleForm)
        axios({
          method:'post',
          url:'http://localhost:9090/update',
          data:this.ruleForm,
        }).then(res=>{
          if(res.data.result){
            console.log(res)
            this.getallinfo()
            this.dialogFormVisible=false
          }else{
            alert(res.data.msg)
          }
        })
     },

      save(){
        // console.log(this.$options.data().ruleForm)
        
         this.dialogFormVisible2=true
         this.form = this.$options.data().form
      },
      submitForm(form){  
         this.$refs[form].validate((validate)=>{
           if(validate){
              axios({
                        method:'post',
                        data:this.form,
                        url:'http://localhost:9090/add',
                      }).then(res=>{
                        if(res.data.result){
                          this.getallinfo();
                          alert(res.data.msg)
                          // this.form=""
                          this.dialogFormVisible2=false
                        }else{
                          alert(res.data.msg)
                        }
                      })
           }else{
              console.log('error submit!!');
              return false;
           }
         })
        
      },
      handleEdit(index, row) {
        
        this.dialogFormVisible=true 
        this.ruleForm=row
        
      },

      handleDelete(index, row) {
        axios({
          method:'delete',
          data:this.tableData[index].username,
          url:'http://localhost:9090/deleteuser',
        }).then(res=>{
            console.log(res.data.result)
            if(res.data.result){
              alert("删除成功")
              this.getallinfo();
            }else{
              alert("删除失败")
            }
        })
      },
      getallinfo(){
        axios({
          methods:'get',
          url:'http://localhost:9090/getall',
        }).then(res=>{
            this.tableData=res.data

            // alert(res.data)
        })
      }
    },
    created(){
      // this.getallinfo();
      this.gettotal()
      // this.currentPage=1
      this.handleCurrentChange(1)
      // this.getallinfo()
    }

    }
</script>

<style>
   .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
  .addbtn{
    position:relative;
    left: -585px;
    
  }
  
</style>


