<template>
  <div>
    
     
  <el-main>
    <el-form ref="form" :model="form" label-width="80px" :rules="rules" :label-position="labelPosition">
  <el-form-item label="题笔登录" style="text-align:center;font-size:20px;height:10px">
  </el-form-item>
  <el-form-item label="用户名:" style="text-align:left" prop="username">
      <el-input v-model="form.username" placeholder="用户名或邮箱"></el-input>
  </el-form-item>
  <el-form-item label="密码:" style="text-align:left" prop="password">
    <el-input v-model="form.password" placeholder="" type="password" ></el-input>
  </el-form-item>
  <el-form-item label="验证码:" style="text-align:left" prop="identifyingCode">
    <el-input v-model="form.identifyingCode" placeholder="" ></el-input>
    
    <img class="verimg" :src="form.imgpath" alt="">
    <el-button type="success" @click="change" style="background-color:gray;margin-top:5px" >换一张</el-button>

  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="onSubmit('form')">登录</el-button>
    <el-button>取消</el-button>
  </el-form-item>
</el-form>
  </el-main>
  </div>
</template>

<script>
import axios from 'axios'
export default {
    name:'Login',
    data(){
      return {
        labelPosition: 'top',
        form: {
          username: '',
          password:'',
          identifyingCode:'',
          imgpath:''
        },
         rules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur'}
          ],
          password:[
            {required:true,message:'请输入密码',trigger:'blur'}
          ],
          identifyingCode:[
            {required:true,message:'请输入验证码',trigger:'blur'}
          ],
         }
      }
    },
    methods:{
      onSubmit(form) {
        this.$refs[form].validate((validate)=>{
          if(validate){
            axios({
            method:'post',
            url:'http://localhost:9090/login',
            data:{
              username:this.form.username,
              password:this.form.password,
              identifyingCode:this.form.identifyingCode, 
            },

        }).then(res=>{
          
          if(res.data.result){
            this.$router.push('/index')
            alert(res.data.msg)
            // alert("登录成功")
          }else{
            alert(res.data.msg)
          }
        })
          }else{
             console.log('error submit!!');
              return false;
          }
        })
       
      },
      change(){
        axios({
          method:'post',
          url:'http://localhost:9090/getvercode',
        }).then(res=>{
          this.form.imgpath=res.data
        })      
      }         
    },
    beforeCreate(){
      axios({
        method:'post',
        url:'http://localhost:9090/getvercode',
      }).then(res=>{
        // console.log(res.data)
        // this.form.identifyingCode=res.data
        this.form.imgpath=res.data
        
      },
      )
    }


    
    
}
</script>

<style>

 .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 5px;
    margin: 100px auto;
    width: 400px;
    height: 480px;
  }
  .verimg{
    width: 100px;
    height: 50px;
    margin-top: 10px;
  }
</style>


