<template>
    <el-main>
        <el-form ref="form" :model="form" :rules="rules" label-width="80px" :label-position="labelPosition" >
            <el-form-item label="用户名:" style="text-align:left" prop="username">
                <el-input v-model="form.username" ></el-input>
            </el-form-item>
            <el-form-item label="邮箱:" style="text-align:left" prop="email">
                <el-input v-model="form.email" placeholder="" type="email" ></el-input>
            </el-form-item>
            <el-form-item label="密码:" style="text-align:left" prop="pass"  autocomplete="off" >
                <el-input v-model="form.pass" placeholder="" type="password" ></el-input>
            </el-form-item>
            <el-form-item label="确认密码:" style="text-align:left" prop="checkPass"  autocomplete="off">
                <el-input v-model="form.checkPass" placeholder="" type="password" ></el-input>
            </el-form-item>
            <el-form-item label="验证码:" style="text-align:left" >
                <el-input v-model="form.identifyingCode" placeholder=""></el-input>
            </el-form-item> 
            <el-form-item>
                <el-button class="resbtn"  type="primary" @click="onSubmit" style="background-color:orange">注册</el-button>
                <!-- <el-button>取消</el-button> -->
            </el-form-item>
        </el-form>
    </el-main>
</template>

<script>
import axios from 'axios'
export default {
    name:'Register',
    data() {
        var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.form.checkPass !== '') {
            this.$refs.form.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.form.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
        };
      return {
        labelPosition: 'top',
        form: {
          username: '',
          email:'',
          pass: '',
          checkPass: '',
          identifyingCode:'',

        },
        rules: {
          username: [
            { required: true, message: '请输入用户名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          email:[
              {required:true,message:'请输入邮箱',trigger:'blur'},
              { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
          ],
          pass:[
            { validator: validatePass, trigger: 'blur' },
            {required:true,message:'请输入密码',trigger:'blur'}
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' },
            {required:true,message:'请确认密码',trigger:'blur'}
          ],
          // identifyingCode:[
          //   {required:true,message:'请输入验证码',trigger:'blur'}
          // ],
         }
      }
    },
    methods: { 
      onSubmit(){
        axios({
          method:'post',
          url:'http://localhost:9090/register',
          data:{
            username:this.form.username,
            email:this.form.email,
            password:this.form.pass
          },
          
        }).then(res=>{
          if(res.data.result){
            //跳转到登录界面
            this.$router.push('/')
            alert('注册成功')
          }else{
            alert(res.Error)
          }
        })
      }

    }
  
}
</script>

<style>
    .el-main {
    background-color: #E9EEF3;
    color: #333;
    /* text-align: center; */
    line-height: 0px;
    height: 573px;
    width: 532px;
    margin: 40px auto;
    padding: 10px;
    border-radius: 15px;

  }
  .el-form-item {
    margin-bottom: 5px;
    }
    .resbtn{
        margin-top: 5px;
    }
   

</style>


